.calendar-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.calendar-container h1 {
    color: #ffffff;
    text-align: center;
    margin-bottom: 20px;
}

.admin-controls {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.admin-controls button {
    margin: 0 10px;
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.admin-controls button:hover {
    background-color: #555;
}

.event-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
}

.event-card {
    background-color: #1a1a1a;
    border-radius: 10px;
    padding: 20px;
    color: #ffffff;
    text-align: center;
    transition: transform 0.3s, box-shadow 0.3s;
}

.event-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(255, 255, 255, 0.1);
}

.event-flag {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 5px;
    margin-bottom: 10px;
}

.event-card h2 {
    margin: 10px 0;
    font-size: 1.2em;
}

.event-card p {
    margin: 5px 0;
    font-size: 0.9em;
}

.edit-button {
    margin-top: 10px;
    padding: 5px 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.edit-button:hover {
    background-color: #45a049;
}

@media (max-width: 768px) {
    .event-grid {
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    }
}